/* Styling for the LoginScreen component */
.loginScreen {
  position: relative;
  height: 100%;
  background: url("https://miro.medium.com/v2/resize:fit:1400/1*5lyavS59mazOFnb55Z6znQ.png") center no-repeat;
  background-size: cover;
}

/* Styles for heading elements */
.loginScreen_body > h1 {
  font-size: 3.125rem;
  margin-bottom: 20px;
}

.loginScreen_body > h2 {
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 20px;
}

.loginScreen_body > h3 {
  font-size: 1rem;
  font-weight: 400;
}

/* Styling for the loginScreen_body container */
.loginScreen_body {
  position: absolute;
  top: 50%; /* Center vertically */
  left: 50%; /* Center horizontally */
  transform: translate(-50%, -50%); /* Center both vertically and horizontally */
  text-align: center;
  z-index: 1;
  color: #fff;
  padding: 20px;
}

/* Gradient overlay on the LoginScreen background */
.loginScreen_gradient {
  width: 100%;
  z-index: 1;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.8) 0,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0.8) 100%
  );
}

/* Sign In button */
.loginScreen_button {
  position: absolute;
  right: 20px;
  top: 20px;
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: red;
  border: none;
  cursor: pointer;
}

/* Netflix logo on the LoginScreen */
.loginScreen_logo {
  position: fixed;
  left: 0;
  width: 150px;
  object-fit: contain;
  padding-left: 20px;
}

/* Input field for email */
.loginScreen_input > form > input {
  padding: 10px;
  outline-width: 0;
  height: 30px;
  width: 30%;
  border: none;
  max-width: 600px;
}

/* Styling for the "Get Started" button */
.loginScreen_getStarted {
  padding: 16px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: red;
  border: none;
  font-weight: 600;
  cursor: pointer;
}
