.nav{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 20px;
    z-index: 1;
    /* animations */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav_contents{
    display: flex;
    justify-content: space-around;
}
.nav_black{
    background-color: black;
}
.nav_logo{
    position: fixed;
    top: 10px;
    left: 0;
    width: 70px;
    height: 55px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}
.nav_avatar{
    cursor: pointer;
    position: fixed;
    right: 20px;
    width: 30px;
}